  color: #f55c47;
  -webkit-animation: blinker 1.5s linear infinite;
          animation: blinker 1.5s linear infinite;
}
.dot {
  height: 12px;
  width: 12px;
  background-color: #f55c47;
  border-radius: 50%;
  display: inline-block;
}

@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

