.button3 {
  text-align: center;
  width: auto;
  height: auto;
  padding: 15px;
  color: black;
  background-color: #37b9f1;
  border-radius: 4px;
  box-shadow: inset 0 0 0 0 #37b9f1;
  transition: ease-out 0.5s;
  font-size: 2rem;
  font-family: "Staatliches", cursive;
  outline: none;
  letter-spacing: 4px;
  text-decoration: none;
  margin: 5px;
}

.button3.Yellow:hover {
  box-shadow: inset 0 100px 0 0 #f5a962;
  cursor: pointer;
  color: white;
}
.button3.Green:hover {
  box-shadow: inset 0 100px 0 0 #66de93;
  cursor: pointer;
  color: white;
}
.button3.Red:hover {
  box-shadow: inset 0 100px 0 0 #ff616d;
  cursor: pointer;
  color: white;
}
h3 {
  font-family: "Pacifico", cursive;
}

.app__right h3 {
  background-color: #3c8dad;
  text-align: center;
  color: white;
  font-size: 20px;

  opacity: 0.9;
  box-shadow: 0 0 20px #bac3c3;
  -webkit-animation: change-background 4s ease infinite;
          animation: change-background 4s ease infinite;
}

@-webkit-keyframes change-background {
  0% {
    background: #3c8dad;
  }
  25% {
    background: #39a6a3;
  }
  50% {
    background: #39a2db;
  }
  75% {
    background: #00ead3;
  }
  90% {
    background: #3edbf0;
  }
  90% {
    background: #3c8dad;
  }
}

@keyframes change-background {
  0% {
    background: #3c8dad;
  }
  25% {
    background: #39a6a3;
  }
  50% {
    background: #39a2db;
  }
  75% {
    background: #00ead3;
  }
  90% {
    background: #3edbf0;
  }
  90% {
    background: #3c8dad;
  }
}

